import React, { useState, useEffect } from "react";
import "./style.css";
import Card from "../UI/Card";
import blogpost from "../../data/blog.json";
import { NavLink } from "react-router-dom";
import { FaLinkedin, FaTwitter, FaGithub, FaBorderNone } from "react-icons/fa";

const Sidebar = (props) => {
    const [posts, setPosts] = useState([]);

    useEffect(() => {
        const posts = blogpost.data;
        setPosts(posts);
    }, [posts]);

    const open = () => {
        console.log("clicked");
    };

    return (
        <div className="sidebarContainer" style={{ width: props.width }}>
            <Card
                style={{
                    marginBottom: "20px",
                    padding: "20px",
                    boxSizing: "border-box",
                }}
            >
                <div className="cardHeader">
                    <span>About Us</span>
                </div>
                <div className="profileimageContainer">
                    <img src={require("../../dp/shradha DP.jpg")} alt="My pic" />
                </div>
                <div className="cardBody">
                    <p className="personalInfo">
                        My name is Shradha Parkhi I am a Front end developer....:)
          </p>
                </div>
            </Card>

            <Card
                style={{
                    marginBottom: "20px",
                    padding: "20px",
                    boxSizing: "border-box",
                }}
            >
                <div className="cardHeader">
                    <span>Recent posts</span>
                </div>

                <div className="recentPosts">
                    {posts.map((post) => {
                        return (
                            <NavLink key={post.id} to={`/post/${post.id}`}>
                                <div className="recentPost">
                                    <h3>{post.blogTitle}</h3>
                                    <span>{post.postedOn}</span>
                                </div>
                            </NavLink>
                        );
                    })}
                </div>
            </Card>
            <Card
                style={{
                    marginBottom: "20px",
                    padding: "20px",
                    boxSizing: "border-box",
                }}
            >
                <div className="cardHeader">
                    <span>
                        <a href="https://www.linkedin.com/in/shradha-parkhi-06054b191/">
                            <FaLinkedin size="30px" color="black" />
                        </a>
                        <a href="https://github.com/shradha1612">
                            <FaGithub size="30px" color="black" />
                        </a>
                        <a href="https://twitter.com/ParkhiShradha">
                            <FaTwitter size="30px" color="black" />
                        </a>
                    </span>
                </div>
            </Card>
        </div>
    );
};
export default Sidebar;
