import React, {useState, useEffect} from 'react'
import './style.css';

import Card from '../../components/UI/Card';
import blogpost from "../../data/blog.json";
import { NavLink } from "react-router-dom";


const Home = (props) => {
   
    const [posts, setPosts] = useState([]);

    useEffect(() => {
        const posts = blogpost.data;
        setPosts(posts);
    }, [posts]);

    const open = () => {
        console.log("clicked");
    };

    return (
        <div>
            
            <Card>
                <div class="masonry">
                    <img src="https://image.shutterstock.com/image-photo/web-development-coding-programming-internet-260nw-1141706672.jpg" />
                    <img src="https://image.shutterstock.com/image-illustration/abstract-futuristic-infographic-visual-data-260nw-746652745.jpg" />
                    <img src="https://image.shutterstock.com/image-photo/web-design-development-chart-keywords-260nw-445183306.jpg" />
                    <img src="https://cdn.pixabay.com/photo/2015/12/04/14/05/code-1076536__480.jpg" />
                    <img src="https://cdn.pixabay.com/photo/2015/02/02/11/09/office-620822__480.jpg" />
                    <img src="https://cdn.pixabay.com/photo/2017/07/15/19/42/manipulation-smartphone-2507499__480.jpg" />
                    <img src="https://cdn.pixabay.com/photo/2016/03/09/09/17/computer-1245714__480.jpg" />
                    <img src="https://cdn.pixabay.com/photo/2016/11/27/21/42/stock-1863880__480.jpg" />
                    <img src="https://cdn.pixabay.com/photo/2016/11/03/18/19/social-media-1795578__480.jpg" />
                    <img src ="https://cdn.pixabay.com/photo/2016/11/08/05/10/students-1807505__480.jpg"/>
                    <img src="https://image.shutterstock.com/image-photo/technology-hands-businessmen-260nw-110678570.jpg"/>
                    <img src="https://cdn.pixabay.com/photo/2014/02/27/16/09/microscope-275984__480.jpg"/>
                </div>   

            </Card>
            <Card
                style={{
                    marginBottom: "20px",
                    padding: "20px",
                    boxSizing: "border-box",
                }}
            >
                <div className="size">
                    <span>Recent posts</span>
                </div>

                <div className="recentPosts">
                    {posts.map((post) => {
                        return (
                            <NavLink key={post.id} to={`/post/${post.id}`}>
                                <div className="recentPost">
                                    <h3>{post.blogTitle}</h3>
                                    <span>{post.postedOn}</span>
                                </div>
                            </NavLink>
                        );
                    })}
                </div>
            </Card>
        </div>
    )


}

export default Home;